import React from "react";
import { StyledFilterBox } from "../styles";

const Select = ({ label, name, onChange, options }) => {
  return (
    <StyledFilterBox>
      <label htmlFor={name}>
        {label}
        <select id={name} name={name} onChange={onChange}>
          {options.map(value => {
            return (
              <option key={`${name} ${value}`} {...{ value }}>
                {value}
              </option>
            );
          })}
        </select>
      </label>
    </StyledFilterBox>
  );
};

export default Select;
